
'use client';

import { useEffect, useState, useTransition } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import type { Competition, CompetitionStatus } from '@/types';
import { getCompetitions, createCompetition, updateCompetition, deleteCompetition } from '@/app/actions';
import { Button } from '@/components/ui/button';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
  DialogClose,
} from '@/components/ui/dialog';
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow, TableCaption } from '@/components/ui/table';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Calendar } from '@/components/ui/calendar';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { useToast } from '@/hooks/use-toast';
import { Loader2, Edit, Trash2, PlusCircle, CalendarIcon as LucideCalendarIcon, ShieldAlert, RefreshCw, Trophy } from 'lucide-react';
import { cn } from '@/lib/utils';
import { format, parseISO } from 'date-fns';

const ALL_COMPETITION_STATUSES: CompetitionStatus[] = ['策划中', '进行中', '评审中', '已结束', '已归档'];

const competitionSchema = z.object({
  name: z.string().min(1, { message: '比赛名称不能为空。' }),
  year: z.coerce.number().min(2000, { message: '年份不能早于2000年。' }).max(2100, { message: '年份不能晚于2100年。' }),
  status: z.enum(ALL_COMPETITION_STATUSES, { required_error: '请选择比赛状态。' }),
  startDate: z.date({ required_error: '请选择开始日期。' }),
  endDate: z.date({ required_error: '请选择结束日期。' }),
  description: z.string().optional(),
}).refine(data => data.endDate >= data.startDate, {
  message: "结束日期不能早于开始日期。",
  path: ["endDate"], // Point to the endDate field for the error
});

type CompetitionFormValues = z.infer<typeof competitionSchema>;

export default function ManageCompetitions() {
  const [competitions, setCompetitions] = useState<Competition[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isSubmitting, startSubmitTransition] = useTransition();
  const { toast } = useToast();

  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [editingCompetition, setEditingCompetition] = useState<Competition | null>(null);

  const form = useForm<CompetitionFormValues>({
    resolver: zodResolver(competitionSchema),
    defaultValues: {
      name: '',
      year: new Date().getFullYear(),
      status: '策划中',
      startDate: new Date(),
      endDate: new Date(new Date().setDate(new Date().getDate() + 30)),
      description: '',
    },
  });

  const fetchCompetitionsData = async () => {
    setIsLoading(true);
    setError(null);
    try {
      const data = await getCompetitions();
      setCompetitions(data);
    } catch (err) {
      console.error('Failed to fetch competitions:', err);
      setError('加载比赛数据失败，请稍后再试。');
      toast({ title: '错误', description: '加载比赛数据失败。', variant: 'destructive' });
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    fetchCompetitionsData();
  }, []);

  const handleDialogOpen = (competition?: Competition | null) => {
    setEditingCompetition(competition || null);
    if (competition) {
      form.reset({
        name: competition.name,
        year: competition.year,
        status: competition.status,
        startDate: parseISO(competition.startDate), // Ensure string dates from server are parsed
        endDate: parseISO(competition.endDate),     // Ensure string dates from server are parsed
        description: competition.description || '',
      });
    } else {
      form.reset({ // Default values for new competition
        name: '',
        year: new Date().getFullYear(),
        status: '策划中',
        startDate: new Date(),
        endDate: new Date(new Date().setDate(new Date().getDate() + 30)),
        description: '',
      });
    }
    setIsDialogOpen(true);
  };
  
  const handleDialogClose = () => {
    setIsDialogOpen(false);
    setEditingCompetition(null);
    form.reset(); // Reset form when dialog closes
  };


  const onSubmit = async (values: CompetitionFormValues) => {
    startSubmitTransition(async () => {
      const apiData = {
        ...values,
        startDate: format(values.startDate, 'yyyy-MM-dd'),
        endDate: format(values.endDate, 'yyyy-MM-dd'),
      };

      let result;
      if (editingCompetition) {
        result = await updateCompetition(editingCompetition.id, apiData);
      } else {
        // Ensure correct type for createCompetition
        const createData: Omit<Competition, 'id' | 'startDate' | 'endDate'> & { startDate: string; endDate: string; } = {
            name: apiData.name,
            year: apiData.year,
            status: apiData.status,
            startDate: apiData.startDate,
            endDate: apiData.endDate,
            description: apiData.description,
        };
        result = await createCompetition(createData);
      }

      if (result.success) {
        toast({ title: '成功', description: result.message });
        fetchCompetitionsData();
        handleDialogClose();
      } else {
        toast({ title: '操作失败', description: result.message, variant: 'destructive' });
      }
    });
  };

  const handleDeleteCompetition = (competitionId: string, competitionName: string) => {
    if (window.confirm(`确定要删除比赛 "${competitionName}" 吗？此操作无法撤销。`)) {
      startSubmitTransition(async () => {
        const result = await deleteCompetition(competitionId);
        if (result.success) {
          toast({ title: '成功', description: result.message });
          fetchCompetitionsData();
        } else {
          toast({ title: '删除失败', description: result.message, variant: 'destructive' });
        }
      });
    }
  };


  if (isLoading) {
    return (
      <div className="flex justify-center items-center py-10">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-3 text-lg">正在加载比赛数据...</p>
      </div>
    );
  }

  if (error) {
    return (
      <Card className="my-10 bg-destructive/10 border-destructive">
        <CardHeader>
          <CardTitle className="flex items-center text-destructive">
            <ShieldAlert className="mr-2 h-6 w-6" />
            加载错误
          </CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-destructive-foreground">{error}</p>
          <Button onClick={fetchCompetitionsData} variant="destructive" className="mt-4">
            <RefreshCw className="mr-2 h-4 w-4" />
            重试
          </Button>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="shadow-lg mt-2">
      <CardHeader className="flex flex-row items-start sm:items-center justify-between gap-2">
        <div>
           <CardTitle className="text-2xl flex items-center">
            <Trophy className="mr-3 h-6 w-6 text-primary" />
            比赛列表
           </CardTitle>
          <CardDescription>创建、编辑和管理建筑比赛项目。</CardDescription>
        </div>
        <Dialog open={isDialogOpen} onOpenChange={ (open) => { if (!open) handleDialogClose(); else setIsDialogOpen(true); }}>
          <DialogTrigger asChild>
            <Button onClick={() => handleDialogOpen(null)}>
              <PlusCircle className="mr-2 h-5 w-5" />
              创建新比赛
            </Button>
          </DialogTrigger>
          <DialogContent className="sm:max-w-2xl max-h-[90vh] overflow-y-auto p-6">
            <DialogHeader>
              <DialogTitle>{editingCompetition ? '编辑比赛' : '创建新比赛'}</DialogTitle>
              <DialogDescription>
                {editingCompetition ? '修改比赛的详细信息。' : '填写新比赛的详细信息。'}
              </DialogDescription>
            </DialogHeader>
            <Form {...form}>
              <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-5 mt-4">
                <FormField
                  control={form.control}
                  name="name"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>比赛名称</FormLabel>
                      <FormControl>
                        <Input placeholder="例如：2024夏季建筑大赛" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="year"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>年份</FormLabel>
                      <FormControl>
                        <Input type="number" placeholder="例如：2024" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                 <FormField
                  control={form.control}
                  name="status"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>状态</FormLabel>
                      <Select onValueChange={field.onChange} defaultValue={field.value}>
                        <FormControl>
                          <SelectTrigger>
                            <SelectValue placeholder="选择比赛状态" />
                          </SelectTrigger>
                        </FormControl>
                        <SelectContent>
                          {ALL_COMPETITION_STATUSES.map(statusVal => (
                            <SelectItem key={statusVal} value={statusVal}>{statusVal}</SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <FormField
                    control={form.control}
                    name="startDate"
                    render={({ field }) => (
                      <FormItem className="flex flex-col">
                        <FormLabel>开始日期</FormLabel>
                        <Popover>
                          <PopoverTrigger asChild>
                            <FormControl>
                              <Button
                                variant={"outline"}
                                className={cn(
                                  "w-full justify-start text-left font-normal",
                                  !field.value && "text-muted-foreground"
                                )}
                              >
                                <LucideCalendarIcon className="mr-2 h-4 w-4" />
                                {field.value ? (
                                  format(field.value, "PPP") // PPP gives format like "Jan 1, 2023"
                                ) : (
                                  <span>选择日期</span>
                                )}
                              </Button>
                            </FormControl>
                          </PopoverTrigger>
                          <PopoverContent className="w-auto p-0" align="start">
                            <Calendar
                              mode="single"
                              selected={field.value}
                              onSelect={field.onChange}
                              disabled={(date) => date < new Date("2000-01-01") } // Example past limit
                              initialFocus
                            />
                          </PopoverContent>
                        </Popover>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  <FormField
                    control={form.control}
                    name="endDate"
                    render={({ field }) => (
                      <FormItem className="flex flex-col">
                        <FormLabel>结束日期</FormLabel>
                        <Popover>
                          <PopoverTrigger asChild>
                            <FormControl>
                              <Button
                                variant={"outline"}
                                className={cn(
                                  "w-full justify-start text-left font-normal",
                                  !field.value && "text-muted-foreground"
                                )}
                              >
                                <LucideCalendarIcon className="mr-2 h-4 w-4" />
                                {field.value ? (
                                  format(field.value, "PPP")
                                ) : (
                                  <span>选择日期</span>
                                )}
                              </Button>
                            </FormControl>
                          </PopoverTrigger>
                          <PopoverContent className="w-auto p-0" align="start">
                            <Calendar
                              mode="single"
                              selected={field.value}
                              onSelect={field.onChange}
                              disabled={(date) => date < (form.getValues("startDate") || new Date("2000-01-01"))}
                              initialFocus
                            />
                          </PopoverContent>
                        </Popover>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                </div>
                <FormField
                  control={form.control}
                  name="description"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>描述 <span className="text-muted-foreground text-xs">(可选)</span></FormLabel>
                      <FormControl>
                        <Textarea placeholder="输入比赛的简要描述和规则..." {...field} rows={3} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <DialogFooter className="pt-4">
                  <DialogClose asChild>
                     <Button type="button" variant="outline" onClick={handleDialogClose}>取消</Button>
                  </DialogClose>
                  <Button type="submit" disabled={isSubmitting}>
                    {isSubmitting && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                    {editingCompetition ? '保存更改' : '创建比赛'}
                  </Button>
                </DialogFooter>
              </form>
            </Form>
          </DialogContent>
        </Dialog>
      </CardHeader>
      <CardContent>
        {competitions.length === 0 && !isLoading ? (
          <p className="text-center text-muted-foreground py-6">暂无比赛记录。请点击上方按钮创建一个新比赛。</p>
        ) : (
          <div className="overflow-x-auto">
            <Table>
              <TableCaption>共 {competitions.length} 个比赛记录。</TableCaption>
              <TableHeader>
                <TableRow>
                  <TableHead className="min-w-[200px] w-[30%]">比赛名称</TableHead>
                  <TableHead className="w-[10%]">年份</TableHead>
                  <TableHead className="w-[15%]">状态</TableHead>
                  <TableHead className="w-[15%]">开始日期</TableHead>
                  <TableHead className="w-[15%]">结束日期</TableHead>
                  <TableHead className="text-right w-[15%] min-w-[120px]">操作</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {competitions.map(comp => (
                  <TableRow key={comp.id}>
                    <TableCell className="font-medium">{comp.name}</TableCell>
                    <TableCell>{comp.year}</TableCell>
                    <TableCell>
                       <span
                        className={cn(
                          "px-2 py-1 rounded-full text-xs font-semibold whitespace-nowrap",
                          comp.status === '进行中' && 'bg-blue-100 text-blue-700',
                          comp.status === '策划中' && 'bg-yellow-100 text-yellow-700',
                          comp.status === '评审中' && 'bg-purple-100 text-purple-700',
                          comp.status === '已结束' && 'bg-green-100 text-green-700',
                          comp.status === '已归档' && 'bg-gray-100 text-gray-700'
                        )}
                      >
                        {comp.status}
                      </span>
                    </TableCell>
                    <TableCell>{format(parseISO(comp.startDate), "yyyy-MM-dd")}</TableCell>
                    <TableCell>{format(parseISO(comp.endDate), "yyyy-MM-dd")}</TableCell>
                    <TableCell className="text-right">
                      <Button variant="ghost" size="icon" onClick={() => handleDialogOpen(comp)} className="mr-1" title="编辑">
                        <Edit className="h-4 w-4" />
                      </Button>
                      <Button variant="ghost" size="icon" onClick={() => handleDeleteCompetition(comp.id, comp.name)} title="删除">
                        <Trash2 className="h-4 w-4 text-destructive" />
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
