
'use client';

import { useState, useEffect } from 'react';
import { useSearchParams, useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { KeyRound, ShieldAlert, FilePenLine, Trophy, Loader2 } from 'lucide-react';
import ManageWorks from '@/components/admin/ManageWorks';
import ManageCompetitions from '@/components/admin/ManageCompetitions';
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { fetchUserSessionForClient } from '@/app/actions'; // Import the action

export default function AdminPage() {
  const [password, setPassword] = useState('');
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isLoadingSession, setIsLoadingSession] = useState(true); // Added for session check
  const [error, setError] = useState<string | null>(null);
  
  const searchParams = useSearchParams();
  const router = useRouter();
  const queryView = searchParams.get('view');
  const initialView = (queryView === 'works' || queryView === 'competitions') ? queryView : 'works';

  useEffect(() => {
    async function checkAuth() {
      setIsLoadingSession(true);
      const storedAuth = sessionStorage.getItem('adminAuthenticated');
      if (storedAuth === 'true') {
        setIsAuthenticated(true);
        setIsLoadingSession(false);
        return;
      }

      // Check main user session
      try {
        const user = await fetchUserSessionForClient();
        if (user && user.type === 'staff') {
          setIsAuthenticated(true);
          // Optionally, set sessionStorage flag here too for consistency if local password is ever used
          // sessionStorage.setItem('adminAuthenticated', 'true'); 
        }
      } catch (e) {
        console.error("Error fetching user session for admin page:", e);
      } finally {
        setIsLoadingSession(false);
      }
    }
    checkAuth();
  }, []);

  const handleTabChange = (value: string) => {
    router.push(`/admin?view=${value}`, { scroll: false });
  };

  useEffect(() => {
    if (queryView !== initialView && (queryView === 'works' || queryView === 'competitions')) {
      // Tabs component's onValueChange handles this
    } else if (!queryView) {
        router.replace('/admin?view=works', { scroll: false });
    }
  }, [queryView, router, initialView]);


  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (password === '123456') {
      setIsAuthenticated(true);
      setError(null);
      sessionStorage.setItem('adminAuthenticated', 'true');
    } else {
      setError('密码错误，请重试。');
      setPassword('');
      sessionStorage.removeItem('adminAuthenticated');
    }
  };

  if (isLoadingSession) {
    return (
      <div className="flex flex-1 justify-center items-center h-full p-4">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-3 text-lg text-muted-foreground">正在验证管理员身份...</p>
      </div>
    );
  }

  if (isAuthenticated) {
    return (
      <div className="flex flex-col flex-1 h-full"> 
        <Tabs defaultValue={initialView} onValueChange={handleTabChange} className="flex flex-col flex-grow">
          <div className="border-b border-border bg-card sticky top-0 z-10">
            <TabsList className="grid w-full grid-cols-2 md:w-auto md:inline-flex h-auto rounded-none bg-transparent p-0">
              <TabsTrigger 
                value="works" 
                className="flex-1 md:flex-initial justify-center gap-2 px-4 py-3.5 text-sm data-[state=active]:bg-primary/10 data-[state=active]:text-primary data-[state=active]:shadow-none rounded-none border-b-2 border-transparent data-[state=active]:border-primary focus-visible:ring-1 focus-visible:ring-ring focus-visible:ring-offset-0"
              >
                <FilePenLine className="h-4 w-4" /> 作品管理
              </TabsTrigger>
              <TabsTrigger 
                value="competitions" 
                className="flex-1 md:flex-initial justify-center gap-2 px-4 py-3.5 text-sm data-[state=active]:bg-primary/10 data-[state=active]:text-primary data-[state=active]:shadow-none rounded-none border-b-2 border-transparent data-[state=active]:border-primary focus-visible:ring-1 focus-visible:ring-ring focus-visible:ring-offset-0"
              >
                <Trophy className="h-4 w-4" /> 比赛管理
              </TabsTrigger>
            </TabsList>
          </div>
          <TabsContent value="works" className="flex-grow mt-0 outline-none ring-0 p-0 md:p-0">
            <div className="p-4 sm:p-6"><ManageWorks /></div>
          </TabsContent>
          <TabsContent value="competitions" className="flex-grow mt-0 outline-none ring-0 p-0 md:p-0">
            <div className="p-4 sm:p-6"><ManageCompetitions /></div>
          </TabsContent>
        </Tabs>
      </div>
    );
  }

  return (
    <div className="flex items-center justify-center flex-1 h-full"> 
      <Card className="w-full max-w-md shadow-xl bg-card text-card-foreground border-border">
        <CardHeader className="text-center">
          <KeyRound className="mx-auto h-12 w-12 text-primary mb-3" />
          <CardTitle className="text-2xl font-bold">管理后台登录</CardTitle>
          <CardDescription>请输入密码以访问管理区域。</CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            <Input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="请输入密码"
              className="text-center"
              aria-label="密码"
            />
            {error && (
              <Alert variant="destructive" className="text-sm border-destructive/50 bg-destructive/10 text-destructive [&>svg]:text-destructive">
                <ShieldAlert className="h-4 w-4" />
                <AlertTitle>登录失败</AlertTitle>
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}
            <Button type="submit" className="w-full bg-primary text-primary-foreground hover:bg-primary/90">
              登录
            </Button>
          </form>
        </CardContent>
         <CardFooter className="text-xs text-muted-foreground pt-6 border-t mt-2">
          <p className="text-center w-full">
            <strong>安全提示:</strong> 此处为演示目的使用客户端密码校验，
            <br />
            请勿在实际生产环境中使用此方式保护敏感数据。
          </p>
        </CardFooter>
      </Card>
    </div>
  );
}
